import PatientDetailsVue from "@/components/Orders/PatientDetails.vue";
import { createApp } from "vue";

let patientApp: any;

export function PatientDialog(show: boolean | undefined, order: any): void {
    const rootContainer: HTMLElement | null = document.getElementById('rootContainer')!;
    if (patientApp) {
        patientApp.unmount();
    }
    patientApp = createApp(PatientDetailsVue, {
        show: show,
        order: order
    });
    patientApp.mount(rootContainer, false);
}
