
import { Client } from "@/composables/client";
import router from "@/router";
import { toastSuccess, toastWarning } from "@/utils/Alerts";
import { MultiStepPopupForm } from "@/utils/PopupKeyboard";
import {
  IonButtons,
  IonButton,
  IonIcon,
  IonModal,
  IonHeader,
  IonContent,
  IonToolbar,
  IonTitle,
  IonItem,
  IonLabel,
  IonFooter,
  IonList,
  IonPage,
  modalController
} from "@ionic/vue";

import { close } from "ionicons/icons";

import { PropType } from "vue";

import { defineComponent } from "vue";
import { FieldType } from "../Forms/BaseFormElements";
import { Option } from "../Forms/FieldInterface";
import Validation from "@/components/Forms/validations/StandardValidations";
import Orders from "@/composables/orders";

export default defineComponent({
  components: {
    IonPage,
    IonButtons,
    IonButton,
    IonIcon,
    IonModal,
    IonHeader,
    IonContent,
    IonToolbar,
    IonTitle,
    IonFooter,
    IonItem,
    IonLabel,
    IonList,
  },
  data(): any {
    return {
      open: true as boolean,
      close: close,
      details: {
        client: {
          date_of_birth: "",
          first_name: "",
          id: "",
          last_name: "",
          middle_name: "",
          sex: "",
        },
      } as any,
    };
  },
  props: {
    order: {
      required: true,
      type: Object as PropType<any>,
    },
    show: {
      required: true,
      default: false,
      type: Boolean as PropType<any>,
    },
  },
  async created() {
    const client = Client();
    const res = await client.getJson(`tests/${this.order.tests[0].id}`);
    if (res?.ok) {
      this.details = res?.data;
    }
  },
  methods: {
    extractedTests(tests: Array<any>): string {
      return tests.map((test) => test.test_type_name).join(", ");
    },
    handleDialog(): void {
      this.open = false;
    },
    async changeSpecimenStatus(status: string): Promise<void> {
      const client = Client();

      const res = await client.putJson(`order_statuses/${status}`, {
        order_id: this.details.order_id,
      });

      if (res?.ok) {
        toastSuccess(`Specimen ${status} successfully!`);
        this.open = false;
        router.push(`/test/details?test_id=${this.details.id}`);
      }
    },
    async rejectOrder(){
      this.open = false;
      router.push(`test/reject/${this.details.id}`);
    },
    loadOrder() {
      Orders().searchbyID(this.details.accession_number);
    },
    async addTest() {
      this.handleDialog();
      const client = Client();
      MultiStepPopupForm(
        [
          {
            id: "test_types",
            helpText: "Select test types",
            type: FieldType.TT_MULTIPLE_SELECT,
            config: {
              showKeyboard: true,
            },
            computedValue: (val: Option[], f: any) => {
              return {
                order_id: this.details.order_id,
                tests: val.map((v: { value: any; label: any }) => {
                  return {
                    specimen: v.value,
                    test_type: v.label,
                  };
                }),
              };
            },
            options: async () => {
              const res = await client.getJson("specimen/test_types", {
                specimen_id: this.details.specimen_id as number,
              });

              console.log(res);
              if (!res?.ok) return [];
              return res.data.map((testType: any) => ({
                label: testType,
                value: this.details.specimen_id,
              }));
            },
            validation: (val: Option) => Validation.required(val),
          },
        ],
        async (_: any, payload: any) => {
          const req = await client.postJson(
            "orders/add_test_to_order",
            payload.test_types
          );
          if (!req?.ok) return toastWarning(req?.error);
          modalController.getTop().then((v) => v && modalController.dismiss());
          this.loadOrder();
        },
        null
      );
    },
  },
});
